.lineup {
    background-color: $rosa;
    padding: $separacion 0;

    h3, p{
        text-align: center;
        color: $blanco;
    }
    .dia{
        text-transform: uppercase;
        margin: $separacion 0;
    }
}

.esenarios-contenedor {
    @include tablet {
        @include grid(2, $separacion);
    }

    .esenario{
        padding-top: $separacion / 2;
    }

    .nombre-esenario{
        color: $negro;
        font-size: 2rem;
        font-weight: $delgada;

        &:after {
            content: '';
            width: 20rem;
            height: 10rem;
            display: block;
            margin: 2rem auto 0 auto;
            background-size: 15rem;
            background-repeat: no-repeat;
            /*alinea verticalmente y si pongo otro center se alinea horizontalmente*/
            background-position: center center;
        }
    }
}

.edm .nombre-esenario {
    &:after{
        background-image: url(/src/img/icono_edm.png);
    }
}

.rock .nombre-esenario {
    &:after{
        background-image: url(/src/img/icono_rock.png);
    }
}

.calendario{
    padding: 0;
    list-style: none;
    li{
        padding: $separacion / 2;
        font-weight: $bold;
        span{
            font-weight: $regular;
        }
    }
}

.bg-verde .calendario li:nth-child(odd){
    background-color: darken($verde, 10);
}

.bg-amarillo .calendario li:nth-child(odd){
    background-color: darken($amarillo, 10);
}