.video {
    position: relative;
    max-height: 70rem;
    overflow: hidden;

    .overlay{
        position: absolute;
        width: 100%;
        height: 100%;
        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+1,9e37f2+67,ffffff+100&0.8+0,0.5+100 */
        background: -moz-linear-gradient(-45deg,  rgba(137,28,30,0.8) 0%, rgba(137,28,30,0.8) 1%, rgba(158,55,242,0.6) 67%, rgba(255,255,255,0.5) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(-45deg,  rgba(137,28,30,0.8) 0%,rgba(137,28,30,0.8) 1%,rgba(158,55,242,0.6) 67%,rgba(255,255,255,0.5) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(135deg,  rgba(137,28,30,0.8) 0%,rgba(137,28,30,0.8) 1%,rgba(158,55,242,0.6) 67%,rgba(255,255,255,0.5) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#cc891c1e', endColorstr='#80ffffff',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
    }
    
    video{
        display: block;
        width: 100%;
    }
}

.contenido-video{
    display: flex;
    align-items: center;
    height: 70%;
    position: relative;
    z-index: 1;

    h2{
        display: flex;
        /*pongo la columna vertical*/
        flex-direction: column;
        /* lo posiciones al iniico y ya no se alarga igual que la fecha */
        align-items: flex-start;
        color: $blanco;
        font-size: 7rem;

        span{
            background-color: $rosa;
            font-size: 2rem;
            padding: 1rem 2rem;
        }
    }
}